import Projectdetail from "./Projectdetail";

class Projectcomp extends React.Component{
    state = {
        project: [
            {
                id:"1",
                title:"skdlf",
                url:""
            },
            {
                id:"2",
                title:"test",
                url:""
            }
        ]
    }
    render(){
        return(
        <div>
            {this.state.project.map(rec,i => (
                <Projectdetail 
                  data = {rec}
                  key={i} />
            ))}
            </div>
        )
    }
}

export default Projectcomp;